<template>
  <div id="tablets">
    <div class="queryBox">
      <Button type="primary" size="small" icon="ios-paper-plane" class="btn-exportBtn" style="margin-right: 10px" @click.stop="handleQlaque">下发</Button>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="plaqueLoading" :columns="plaqueColumns1" :data="plaqueData1" highlight-row :max-height="plaqueTableHeight"
               ref="plaqueMultipleTable"
               @on-selection-change="handleQlaqueSelectionChange"
        >
        </Table>
        <div class="page_box">
          <Page :total="plaqueTotal" ref="plaquePages" :page-size="plaquePageSize" :current.sync="plaquePageNum" @on-change="plaquePageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 下发人员列表-->
    <IssueList ref="issueList" :plaqueId="plaqueId" :taskId="taskId" @getQlaques="getQlaques"></IssueList>
  </div>
</template>

<script>
import IssueList from './IssueList'
import {
  // 表格列表
  getQlaqueList
} from '@/api/taskScheduler'
export default {
  name: 'plaqueList',
  data () {
    return {
      /* 表格高度 */
      plaqueTableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格加载 */
      plaqueLoading: false,
      /* 表格数据 */
      plaqueColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.plaquePageNum - 1) * this.plaquePageSize + 1)
          }
        },
        {
          title: '斑块名称 ',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 150,
          key: 'activeName'
        },
        {
          title: '活动类型',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 150,
          key: 'activeType'
        },
        {
          title: '活动设施名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 150,
          key: 'activityName'
        },
        {
          title: '核查状态',
          align: 'center',
          key: 'ck088',
          minWidth: 100,
          render: (h, params) => {
            let data = params.row.st4ScsCk.ck088
            if (data === '0') {
              return h('span', {}, '未核查')
            } else {
              return h('span', {}, '已核查')
            }
          }
        },
        {
          title: '下发状态',
          align: 'center',
          key: 'sendStatus',
          minWidth: 100,
          render: (h, params) => {
            let data = params.row.sendStatus
            if (data === 0) {
              return h('span', {}, '未下发')
            } else {
              return h('span', {}, '已下发')
            }
          }
        }
      ],
      plaqueData1: [],
      /* 总条数 */
      plaqueTotal: 0,
      /* 初始化页码 */
      plaquePageNum: 1,
      /* 页容量自适应 */
      plaquePageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      // 斑块id
      plaqueId: '',
      // 多选相关参数
      selectQlaqueRow: [],
      multipleQlaqueSelection: []
    }
  },
  components: {
    // 下发人员列表
    IssueList
  },
  watch: {
    multipleQlaqueSelection (data) { // 存储选中的row
      this.selectQlaqueRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectQlaqueRow.push(item.cd001)
        })
      }
    }
  },
  props: {
    taskId: {}
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.plaqueTableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.plaquePageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getQlaques(this.taskId)
    }, false)
  },
  methods: {
    /* 获取字段列表 */
    getQlaques (e) {
      this.plaqueLoading = true
      getQlaqueList({
        data: {
          cl001: e,
          pageSize: this.plaquePageSize,
          pageNumber: this.plaquePageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.plaqueData1 = res.data.data.rows
          this.plaqueTotal = res.data.data.total
          console.log(res.data.data.rows)
          // 这里的就时给数据添加_disabled属性
          res.data.data.rows.forEach(item => {
            if (item.sendStatus !== 0) {
              item['_disabled'] = true
            }
          })
          this.plaqueLoading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    plaquePageChange (pageNum) {
      this.plaquePageNum = pageNum
      this.getQlaques(this.taskId)
    },
    // 勾选checkbox事件
    handleQlaqueSelectionChange (val) {
      this.multipleQlaqueSelection = val
    },
    // 下发任务
    handleQlaque () {
      if (this.selectQlaqueRow.length < 1) {
        this.$Notice.error({ title: '斑块不能为空！' })
      } else {
        this.plaqueId = this.selectQlaqueRow
        this.$refs.issueList.issue = true
      }
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablets{
    position: relative;
    .queryBox {
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      text-align: right;
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px 0;
        text-align: right
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
